body{
    background-image: linear-gradient(to left, orange 0%, yellow 100%);
}
h1{
    text-align: center;
    font-size: clamp(5rem, 3rem, 1rem);
    color: white;
    -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
}
form{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
input{
    width: 30%;
    margin-right: 2rem;
    border-radius: 10px;
    font-size: clamp(3rem,2rem,1rem);
    padding: 1rem;
}
.task-submit{
border-radius: 10px;
background-image: linear-gradient(to left, red 0%, orangered 100%);
color: white;
font-size: 1.5rem;
padding: 1rem;
margin-right: 2rem;
cursor: pointer;
transition: all 0.3s;
}
.task-submit:hover{
    content: '';
 background: transparent;
 color: black;
 border: solid 2px red;  
}
select{
    font-size: 1rem;
    padding: 1rem;
}
ul{
    margin-top: 5rem;
}
li{
    display: inline-flex;
    align-items: center;
    justify-content: left;
    color: white;
    width: 50vw;
    height: 5rem;
    margin-left: 20%;
    padding: 1rem;
    background-color: rgba(0, 0, 0, 0.2);
    font-size: 5rem;
}
.delete{
border-radius: 10px;
background:red;
padding: 1rem;
margin-right: 2rem;
cursor: pointer;
}
.check{
    border-radius: 10px;
    background:green;
    padding: 1rem;
    margin-right: 2rem;
    margin-left: 2rem;
    cursor: pointer;
}
.completed{
    text-decoration: line-through;
}